import React, { Component } from 'react'
import { TypePopup, getTypeselectionFrom } from "./TypePopup"
import { Type } from '../../letcode-code-runner/type'
import { LetCodeTypeMapper } from "../../letcode-code-runner/letcode-mapper"

class CreateQuestion extends Component {
    state = {
        title: '',
        description: '',
        functionName: "",
        returnDescription: "",
        returnType: null,
        currentInputName: "",
        currentInputDescription: "",
        currentInputType: null,
        inputs: [{inputName: "", inputDescription: "", inputType: null}],
        testcases: [{inputs: [], outputs: null, description: ""}],
        popUps: []
    }

    handleChange(e) {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    pushModal(type, onComplete) { 
        const newPopUps = this.state.popUps
        newPopUps.push(() => <TypePopup type={type}
            onComplete={onComplete}
            onCancel={this.popLast.bind(this)}
            pushModal={this.pushModal.bind(this)} />)
        this.setState({ popUps: newPopUps });
    }

    popLast() { 
        const newPopUps = this.state.popUps
        newPopUps.pop()
        this.setState({ popUps: newPopUps });
    }

    handleTypeSelection(e) { 
        if (e.target.value === "dict" || e.target.value === "list") {
            this.pushModal(e.target.value, type => {
                this.setState({ [e.target.id]: type })
            });

        }
        else { 
            this.setState({
                [e.target.id]: {
                    "int": Type.INT,
                    "float": Type.FLOAT,
                    "bool": Type.BOOL,
                    "string": Type.STRING
                }[e.target.value]
            });
        }
    }

    handleAddInput = (e) =>{
        e.preventDefault()
        console.log("add input")
        const inputs = [
            ...this.state.inputs,
            {inputName: "", inputDescription: "", inputType: null}
        ]
        this.setState({
            inputs: inputs
        })
    }

    handleDelete = (e, index) => {
        e.preventDefault()
        const inputs = [
            ...this.state.inputs.slice(0, index),
            ...this.state.inputs.slice(index + 1)
        ]
        this.setState({
            inputs: inputs
        })
    }

    handleChangeInput = (e, index, type) =>{
        e.preventDefault()
        let input;
        switch(type){
            case 'inputName':
                input = {...this.state.inputs[index], inputName: e.target.value}
                break
            case 'inputDescription':
                input = {...this.state.inputs[index], inputDescription: e.target.value}
                break
            // case 'inputType':
            //     input = {...this.state.inputs[index], inputType: e.target.value}
            default:
                input = {}
        }
        const inputs = [
            ...this.state.inputs.slice(0, index),
            input,
            ...this.state.inputs.slice(index + 1)
        ]
        this.setState({
            inputs: inputs
        })
    }


    handleNext = (e) => {
        e.preventDefault();
        console.log("create question", this.state)
        this.props.history.push({
            pathname: '/addtestcase',
            state: this.state
        })
        // check all box are fullfilled and type are satisfied requirement
    }

    render() {

        return (
            <div className="container">
                <div className="row">
                    <div className="col">
                       <h2>New Question</h2>
                    </div>
               </div>
                <form>
                    <label for="title">Title:</label>
                    <input className="form-control" type="text" placeholder="Enter title" id="title" onChange={this.handleChange.bind(this)}/>
                    <label for="description">Description:</label>
                    <textarea className="form-control" id='description' placeholder="Enter description here" onChange={this.handleChange.bind(this)} rows={10} />
                    
                    <label for="create-question-function"><h4>Describe Your Function:</h4></label>
                    <div class="create-question-function-div" id="create-question-function">
                        <div class="create-question-function-name">
                            <label for="functionName">Function Name:</label>
                            <input id="functionName" className="form-control" aria-describedby="functionNameHint" onChange={this.handleChange.bind(this)} />
                            <small id="functionNameHint" className="text-muted">
                                Must be 4-20 characters long. You can use whatever naming convention you like, but no space is allowed.
                            </small>
                        </div>

                        <label for="create-question-input">Input parameters:</label>
                        {
                            this.state.inputs.map((input, index) => (
                                <div>
                                    <div>
                                        <p>input parameter {index}:</p>
                                    </div>
                                    <div class="form-row" id="create-question-input">

                                        {getTypeselectionFrom(this.handleTypeSelection.bind(this), "Select Input Type",
                                        this.state.currentInputType ? LetCodeTypeMapper.mapType(this.state.currentInputType) : "selected",
                                        ["int", "float", "string", "bool", "dict", "list"], "currentInputType")}
                                        
                                        <div class="col col-md-4">
                                            <input type="text" class="form-control" id="inputName"
                                                placeholder="name" value={this.state.inputs[index].inputName} onChange={(e) => this.handleChangeInput(e, index, "inputName")} />
                                        </div>
                                        <div class="col">
                                            <input type="text" class="form-control" id="inputDescription"
                                                placeholder="description of this input parameter" value={this.state.inputs[index].inputDescription} onChange={(e) => this.handleChangeInput(e, index, "inputDescription")} />
                                        </div>
                                    </div>
                                    <button className="blue" onClick={(e) => this.handleDelete(e, index)}>delete</button>
                                </div>
                            ))
                        }
                        <div className="row">
                            <button className="center blue" onClick={(e) => this.handleAddInput(e)}>add new input</button>
                        </div>

                        {/* <div class="form-row" id="create-question-input">
                            {getTypeselectionFrom(this.handleTypeSelection.bind(this), "Select Input Type",
                                this.state.currentInputType ? LetCodeTypeMapper.mapType(this.state.currentInputType) : "selected",
                                ["int", "float", "string", "bool", "dict", "list"], "currentInputType")}
                            <div class="col col-md-3">
                                <input type="text" class="form-control" id="inputName"
                                    placeholder="name" onChange={this.handleChange.bind(this)} />
                            </div>
                            <div class="col col-md-5">
                                <input type="text" class="form-control" id="inputDescription"
                                    placeholder="description of this input parameter" onChange={this.handleChange.bind(this)} />
                            </div>
                            <button className="center" onClick={(e) => this.handleAddInput(e)}>add new input</button>
                        </div> */}

                        <label for="create-question-return">Return:</label>
                        <div class="form-row" id="create-question-return"> 
                            {getTypeselectionFrom(this.handleTypeSelection.bind(this), "Select Return Type",
                                this.state.returnType ? LetCodeTypeMapper.mapType(this.state.returnType) : "selected",
                                ["int", "float", "string", "bool", "dict", "list"], "returnType")}
                            <div class="col col-md-8">
                                <input type="text" class="form-control" id="returnDescription"
                                    placeholder="description of the return value" onChange={this.handleChange.bind(this)}/>
                            </div>
                        </div>
                    </div>
                        <button className="blue center" onClick={(e) => this.handleNext(e)}>Next</button>
                </form>
                {this.state.popUps.map((x) => x())}
            </div>)
    }
}

export default CreateQuestion
