import React, {useState} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import Card from 'react-bootstrap/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import "./cards.css"
import { Star} from '@icon-park/react'
import { grey } from '@material-ui/core/colors';


function DailyQuestionCard(props){
    
    
    const [collection, setcollection] = useState(true)
    var changeCollection = (e) => {
        e.preventDefault()
        if (collection == false){
          setcollection(true)
        }
        else{
          setcollection(false)
        }
        console.log("question of the day status:", collection)
      }
    
    return (
        <Card>        
        <CardContent>
            <div class="row">
                <div class="col" >
                <div class="block" className="star-icon mystar">
                        <Star theme={collection ? "outline" : "filled"} size="30" fill={collection ? "#333" : "orange" } onClick={changeCollection}/></div>
                    <div class="row">
                    <div class="block" className="question-of-the-day-topic"  onClick={()=>{console.log("this is question of the day")}}>{props.items.title}</div>
                    </div>   
                    <br></br>
                    <div className="question-description"> {props.items.description} </div>
                    <br></br>
                    <div class="row">
              <div class="block" className="upvote-number">upvotes: </div>
              <div class="block" className="reply-number">replies: </div>
            </div>
                </div>
                <div class="col">
                    <div className="question-of-the-day-reply">
                        replies heree
                        <br></br>
                        and here
                        <br></br>
                        and here
                        question reply
                    </div>
                </div>
            </div>
          </CardContent> 
        </Card>     
    )
}

export default DailyQuestionCard;
