import React, { useState } from 'react'
import QuestionDesc from './QuestionDesc'
import Answers from './Answers'
import TestCases from './TestCases'
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col, Button } from 'reactstrap'
import classnames from 'classnames'

const QuestionContainer = (props) => {
    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }

    return (
        <div id="postContent">
            <Row>
                <Col sm="12">
                    <QuestionDesc></QuestionDesc>
                </Col>
            </Row>
            <Nav tabs>
                <NavItem>
                    <NavLink
                        className={classnames({ active: activeTab === '1' })}
                        onClick={() => { toggle('1'); }}
                    >
                        Answers
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: activeTab === '2' })}
                        onClick={() => { toggle('2'); }}
                    >
                        Test cases
                    </NavLink>
                </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
                <TabPane tabId="1">
                    <Row>
                        <Col sm="3">
                            <Button color="link">Add answer</Button>
                        </Col>
                        <Col sm={{ size: 'auto', offset: 5 }}>
                            <Button color="link">Sort by newest</Button>
                            <Button color="link">Sort by most upvote</Button>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm="12">
                            <Answers></Answers>
                        </Col>
                    </Row>
                </TabPane>
                <TabPane tabId="2">
                    <Row>
                        <Col>
                            {/* <TestCaseViewModal></TestCaseViewModal> */}
                            <TestCases></TestCases>
                        </Col>
                    </Row>
                </TabPane>
            </TabContent>
        </div>
    )
}


export default QuestionContainer
