const postUser = (user) => {
    console.log("fetch called")
    return fetch('/user/add_user', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(user)
    });
}

const userSignIn = (user) => {
    console.log('userSignIn called')
    return fetch('/user/userSignIn', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(user)
    });
}

module.exports = {
    postUser,
    userSignIn
}