
import React, { Component } from 'react'
import { getQuestion } from '../../api_code/GetFromBackend'
import { Container, Row, Col } from 'react-bootstrap'


class QuestionDesc extends Component {
    state = {
        id: null,
        title: '',
        desc: ''
    }

    componentDidMount() {
        let question = getQuestion()
        question.then((value) => {
            this.setState({
                id: value.id,
                title: value.title,
                desc: value.desc
            })
        })
    }
    
    render() {
        return (
            <Container>
                <Row>
                    <Col>
                        <br></br>
                        <h4>Question #{this.state.id}: {this.state.title}</h4>
                        <br></br>
                        <p>{this.state.desc}</p>
                    </Col>
                </Row>
            </Container>
        )
    }
    
}

export default QuestionDesc