import React, {useState} from 'react'
import { Star} from '@icon-park/react'


function TitleCard(props){


  const [collection, setcollection] = useState(true)

  var changeCollection = (e) => {
    e.preventDefault()
    if (collection === false){
      setcollection(true)

    }
    else{
      setcollection(false)
    }
    console.log("question status:", collection)
  }
    return (
        <div class="card"> 
        <div class="block" className="star-icon mystar">
          <Star onClick={changeCollection} theme={collection ? "outline" : "filled"} size="30" fill={collection ? "#333" : "orange" }/></div>  
        <div class="card-body" onClick={()=>console.log("question")}>
            <div class = "col">
            <div class="row">
              <div class="block" className="question-title">{props.items.title}</div>         
            </div>   
            <br></br>
            <div className="question-description"> {props.items.description} </div>
            <br></br>
            <div class="row">
              <div class="block" className="upvote-number">upvotes: </div>
              <div class="block" className="reply-number">replies: </div>
            </div>
            </div>  
          </div>       
      </div>
)
}

export default TitleCard;
