import React, { Component } from 'react'

class AddTestcase extends Component {

    // use this state to submit a problem
    state = {...this.props.location.state }

    handleAddTestcase = (e) => {
        e.preventDefault()
        const testcase = [
            ...this.state.testcases,
            {inputs: [], outputs: null, description: ""}
        ]
        this.setState({
            testcases: testcase
        })
    }

    handleInputOnchange = (e, inputIndex, testcaseIndex) => {
        e.preventDefault()
        const input = [...(this.state.testcases[testcaseIndex].inputs.slice(0, inputIndex)),
        e.target.value,
        ...this.state.testcases[testcaseIndex].inputs.slice(inputIndex + 1)
        ]
        const testcase = {...this.state.testcases[testcaseIndex], inputs: input}
        const testcases = [
            ...this.state.testcases.slice(0, testcaseIndex),
            testcase,
            ...this.state.testcases.slice(testcaseIndex + 1)
        ]
        console.log("testcase:", testcases)
        this.setState({
            testcases: testcases
        })
        }

    handleOnchange = (e, index, type) => {
        e.preventDefault()
        let testcase
        if (type === "output"){
            testcase = {...this.state.testcases[index], outputs: e.target.value}
        }
        if (type === "description"){
            testcase = {...this.state.testcases[index], description: e.target.value}
        }
        const testcases = [
            ...this.state.testcases.slice(0, index),
            testcase,
            ...this.state.testcases.slice(index + 1)
        ]
        this.setState({
            testcases: testcases
        })
    }

    handlePost = (e) => {
        e.preventDefault()
        console.log("Post question triggered")
        // handle check
    }

    render() {
        console.log(this.props.location.state)
        return (
            <div className="container">
                {  this.state.testcases.map((testcase, ind) => {
                    return (
                        <div>
                            <br></br>
                            <div className="row">
                                <h5>Test case {ind}:</h5>
                            </div>
                            <div className="row">
                                <h5>Input:</h5>
                            </div>
                            {
                                this.state.inputs.map((input, index) => {
                                return(
                                    <div className="form-group row">
                                        <label className="col-sm-2 col-form-label">{input.inputName}:</label>
                                        <div className="col-sm-10">
                                            <input type="text" className="form-control" onChange={(e) => this.handleInputOnchange(e, index, ind)}></input>
                                        </div>
                                    </div>
                                )
                                }
                                )
                            }

                            <div className="row">
                                <h5> Expect output:</h5>
                            </div>
                            <div className="form-group">

                                <div className="row">
                                    <input type="text" className="form-control" onChange={(e) => this.handleOnchange(e, ind, "output")}></input>
                                </div>
                                
                            </div>
                            
                            <div className="row">
                                <textarea class="form-control" placeholder="Add description / message" rows="3" onChange={(e) => this.handleOnchange(e, ind, "description")}></textarea>
                            </div>
                        </div>
                    )
                }
                )
                }
                <button className="blue center" onClick={(e) => this.handleAddTestcase(e)}>Add testcase</button>
                <br></br>
                <button className="blue center" onClick={(e) => this.handlePost(e)}>Post Question</button>
            </div>
        )
    }
}


export default AddTestcase
