import React, { Component } from 'react'
import Editor from './Editor'
import { getAnswers } from '../../api_code/GetFromBackend'
import { UpOne, DownOne } from '@icon-park/react'
import "./answers.css"
import profilePic from '../../img_source/placeholder.png'

class Answers extends Component {
    state = {
        answers: []
    }

    componentDidMount() {
        getAnswers().then(value => {
            this.setState({
                answers: value
            })
        })
    }

    changeCode = (code) => {
        return
    }

    render() {
        const answerList = this.state.answers.length ? (
            this.state.answers.map((answer, index) => {
                
                return (
                    <div className="indanswer" key={index}>
                        <div className="answerProfile">
                            <img className="answerProfilePic" src={answer.user.profilePic} alt="profile"></img>
                            <h4 className="answerHeader">{answer.user.username}</h4>
                        </div>

                        <div className="answerComment">
                            <h5>comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment comment</h5>
                        </div>

                        <div>
                            {/* <p className="answerText">{answer.detail.code}</p> */}
                            <Editor
                                lang={answer.detail.language}
                                changeCode={this.changeCode}
                                code={answer.detail.code}
                                readOnly={true}
                            >
                            </Editor>
                        </div>
                        <div className="answerReply">
                            <h5>replies</h5>
                            <div className="row">
                                <img className='replyImg' src={profilePic} alt="reply"></img>
                                <h6>username</h6>
                            </div>
                            <div className='commentSolution'>
                                <p>comment on the solution</p>
                            </div>

                        </div>
                        <div className="answerButtons">
                            <button className="answerButton">
                                {answer.upvotes}
                                <UpOne theme="outline" size="24" fill="#333" />
                            </button>
                            <button className="answerButton" >
                                {answer.downvotes}
                                <DownOne theme="outline" size="24" fill="#333" />
                            </button>
                        </div>
                    </div>
                )
            })
        ) : (
                <div>No answers</div>
            )
        return (
            <div>
                {answerList}
            </div>
        )
    }
}

export default Answers


