import React from "react";
import { Route, Switch } from 'react-router-dom'
import Header from './react-components/layout/Header'
import MainLogin from './react-components/auth/MainLogin'
import MainPage from './react-components/mainpage/index'
import Profile from './react-components/user/Profile'
import CreateQuestion from './react-components/post-question/CreateQuestion'
import QuestionPage from './react-components/question-page/QuestionPage'
import AnswerQuestion from './react-components/answer-question/answer-question'
import AddTestcase from './react-components/post-question/AddTestcase'
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.css'

// for testing 
import pythonRunner from './letcode-code-runner/python/code-runner-python-impl'
import { Type, VariableType, FunctionType } from './letcode-code-runner/type'
import { TestCase } from './letcode-code-runner/code-runner'
import { StringToLetCodeCodeMapper, LetCodeTypeMapper } from './letcode-code-runner/letcode-mapper'
import { LetCodeToPythonCodeMapper , PythonTypeMapper } from './letcode-code-runner/python/python-mapper'

function testRunner() { 
  // let say we want to test a function that return max value of a list;
  // function name: maxList,  input l: list of int, return int
  // we first define these types using Type:

  const inputType = Type.LIST(Type.INT);
  const outType = Type.INT;

  // you can define nested type like this:
  const myType = Type.DICT(Type.STRING, Type.DICT(Type.FLOAT, Type.LIST(Type.INT)));

  // the LetCodeCodeMapper can map users input value to LetCode standard form, and do the type check
  console.log("letcode type:", LetCodeTypeMapper.mapType(myType))
  console.log("python type:", PythonTypeMapper.mapType(myType))
  console.log(StringToLetCodeCodeMapper.mapCode("{\"a\":{5: [1, 2]}, 'b':{.4: [2, 2]} }", myType))
  // in case this type check failed, it will raise error:
  try { console.log(StringToLetCodeCodeMapper.mapCode("{\"a\":\"s\", 'b':{.4: [2, 2]} }", myType)) } catch (err) { console.error(err) }

  // OK  back to the function we want to define
  const myFunc = new FunctionType("maxList", [new VariableType("l", inputType)], new VariableType("return", outType))

  // test cases:
  const onTestCase1Succ = (err) => console.log("testcase 1 pass: test output " + err);
  const onTestCase2Succ = (err) => console.log("testcase 2 pass: test output " + err);
  const onTestCase3Succ = (err) => console.log("testcase 3 pass: test output " + err);
  const onTestCase1Failed = (err) => console.log("testcase 1 failed: test output " + err);
  const onTestCase2Failed = (err) => console.log("testcase 2 failed: test output " + err);
  const onTestCase3Failed = (err) => console.log("testcase 3 failed: test output " + err);
  const tc1 = new TestCase([LetCodeToPythonCodeMapper.mapCode("[1, 2, 3]", outType)], LetCodeToPythonCodeMapper.mapCode("3", outType), onTestCase1Succ, onTestCase1Failed)
  const tc2 = new TestCase([LetCodeToPythonCodeMapper.mapCode("[1, 2, 3, 4]", outType)], LetCodeToPythonCodeMapper.mapCode("4", outType), onTestCase2Succ, onTestCase2Failed)
  const tc3 = new TestCase([LetCodeToPythonCodeMapper.mapCode("[1]", outType)], LetCodeToPythonCodeMapper.mapCode("1", outType), onTestCase3Succ, onTestCase3Failed)
  // choice a runner
  const runner = new pythonRunner();
  console.log("can python runner suppport this?", runner.canSupportFunctionType(myFunc));
  // set console
  runner.setConsole(x => console.log("python console: " + x), x => console.warn("python console: error: " + x));
  // init, we pass a function that will be called on state change
  let run = true
  runner.init(x => {
    console.log("python runner state: " + x)
    // run our test if runner is ready
    if (x === "ready" && run) { 
      runner.runTest(myFunc, [],
        `
def maxList(l):
    print("i'm in python!")
    if len(l) == 4:
        raise Exception("my exception")
    return l[-1]
    `
      );
      run = false;
    }
  });
  // get starter code:
  console.log(runner.getStarterCode(myFunc));
  // run tests
  
}

function App() {
  // testRunner();
  return (
    <div className="App">
      <Header></Header>
      <Switch>
        <Route exact path="/" component={MainPage}></Route>
        <Route path='/signin_up' component={MainLogin}></Route>
        <Route path='/:username/profile' component={Profile}></Route>
        <Route path='/newquestion' component={CreateQuestion}></Route>
        <Route path='/:question_id/detail' component={QuestionPage}></Route>
        <Route path='/:question_id/answer_question' component={AnswerQuestion}></Route>
        {/* <Route path='/:username/collections' component={Collection}> */}
        <Route path='/addtestcase' component={AddTestcase}></Route>
        <Route path='/myprofile' component={Profile}></Route>
      </Switch>
    </div>
  );
}

export default App;
